.profile-container {
  height: calc(100vh - 35px);
  display: flex;
}

.profile-back {
  width: 64px;
  background: #fbfbfb;
  justify-content: center;
  align-items: center;
  display: flex;
}

.profile-back__arrow {
  width: 28px;
  height: 28px;
  cursor: pointer;
  background: #3369f3;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.profile-separator {
  width: 2px;
  height: 100%;
  background: #eaeaea;
}

.profile {
  width: 510px;
  flex-direction: column;
  align-self: center;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.profile__avatar {
  width: 130px;
  height: 130px;
  cursor: pointer;
  background: #efefef;
  border-radius: 50%;
  justify-content: center;
  margin-bottom: 21px;
  display: flex;
  position: relative;
}

.profile__avatar:hover .change-avatar {
  display: flex;
}

.change-avatar {
  opacity: .5;
  color: #fff;
  text-align: center;
  background: #000;
  align-items: center;
  font-size: 16px;
  display: none;
  position: absolute;
}

.profile__title {
  color: #1e1e1e;
  margin-bottom: 50px;
  font-size: 18px;
  font-weight: 600;
}

.profile__info {
  width: 100%;
  height: 34px;
  border-bottom: 1px solid #eaeaea;
  justify-content: space-between;
  align-items: center;
  font-size: 16px;
  font-weight: 500;
  display: flex;
}

.profile div:nth-child(8) {
  border-bottom: none;
  margin-bottom: 40px;
}

.profile div:nth-child(11) {
  color: red;
  border-bottom: none;
}

.profile__info a {
  color: #3369f3;
}

.profile__info_key {
  color: #1e1e1e;
}

.profile__info_value {
  color: #999;
}

